import QrcodeVue from 'qrcode.vue';
import ToolBar from '../ToolBar.vue';
import WalletBar from '../WalletBar.vue';
import { InvoiceData } from "../../ts/invoice";
import { StorageAccessor } from "../../ts/storage";
